@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=PT+Mono&display=swap);
body {
    padding: 0;
    margin: 0;
}
/* TYPOGRAPHY START */
.primary {
  font-family: "Lato", sans-serif !important;
  color: #141414;
}

.decorate {
  font-family: "Lato", sans-serif !important;
  color: #31ab65;
}

.secondary {
  font-family: "PT Mono", monospace !important;
  color: #141414;
  text-transform: uppercase !important;
}

.sub-header {
  font-weight: 900 !important;
  letter-spacing: -0.05rem;
  line-height: 2.8rem !important;
}

.header {
  margin-top: 25px !important;
  font-weight: 900 !important;
  letter-spacing: -0.1rem;
  line-height: 2.8rem !important;
}

.header-mini {
  font-family: "Lato", sans-serif !important;
  margin-top: 5px !important;
  line-height: 18px !important;
  color: grey;
  font-weight: 900 !important;
  letter-spacing: -0.05rem;
}

.body {
  margin-top: 0.3rem !important;
}

.span {
  letter-spacing: 0rem !important;
}

/* TYPOGRAPHY END */

/* BUTTONS START */
.btn-fill {
  margin: 1rem;
  border: 3px solid;
  font-family: "Lato";
  font-weight: 900;
  font-size: 18px;
  color: #0e0e0e;
  cursor: pointer;
  width: 145px;
  height: 45px;
  display: inline-block;
  letter-spacing: -0.01rem;
  position: relative;
  background: #ebebeb;
  white-space: nowrap;
}

.long {
  width: 165px !important;
}
.btn-bottom {
  box-shadow: -5px 5px #0e0e0e;
  border-radius: 3px;
}

.btn-bottom:active {
  box-shadow: 0 0 #0e0e0e;
  top: 5px;
}

button:focus {
  outline: 0;
}

.link {
  font-family: "Lato", sans-serif !important;
  font-weight: 900 !important;
  color: #31ab65 !important;
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
}

.link:hover {
  background: transparent;
  box-shadow: 0px 0px 0px transparent;
  border: 0px solid transparent;
  text-shadow: 0px 0px 0px transparent;
}

.link:active {
  outline: none;
  border: none;
}

/* BUTTONS END */

/* NAV START */
.nav {
  background-color: transparent;
}

.logo {
  width: 250px;
  height: auto;
  margin: 10px;
}
/* NAV END */

/* LANDING START */
.landing {
  width: 100vw;
  height: 100vh;
  background-color: #31ab65;
}

.disclaimer {
  margin-top: 0rem;
}

.button-wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: flex-start;
}
/* LANDING END */

/* SEARCH START */
.suggestions {
  margin-top: 0px;
}

.suggestion-links {
  text-decoration: underline !important;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
/* SEARCH END */

/* STRAIN COMPONENT START */
.strain-header-wrapper {
  margin-bottom: 10px;
}

.other-strains-wrapper {
  margin-top: 15px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: nowrap;
}

.other-strains {
  width: 74px;
  margin: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid;
  border-color: #31ab65;
  border-radius: 5px;
  padding: 5px;
}

.other-strain-image {
  max-width: 50px;
  width: 100%;
  height: auto;
}

.strain-image {
  max-width: 500px;
  width: 100%;
  height: auto;
}

.colored-divider {
  margin-top: 10px !important;
  background: #31ab65 !important;
  width: 100% !important;
}

.other-strain-typography {
  text-align: left;
  align-self: flex-start;
}

.back-button-wrapper {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  align-content: center;
  justify-content: center;
}
/* STRAIN COMPONENT END */

